import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            info: null,
            ageStr: ''
        }
    },
    created(){
        this.pull()
    },
    methods:{
        pull(){
            this.$get('PlatFormAPI/ServicePackItem/GetServicePackRecordDetail',this.$pick({
                ServicePackRecordId: this.$route.query.id
            })).then(o => {
                this.info = o
                this.age(o.PatientInfo.Birthday)
            })
        },
        age(str){
            var birthday=new Date(str.replace(/-/g, "\/")); 
            var d=new Date();
            this.ageStr = d.getFullYear()-birthday.getFullYear()-((d.getMonth()<birthday.getMonth()|| d.getMonth()==birthday.getMonth() && d.getDate()<birthday.getDate())?1:0);
        },
        toCheck(){
            this.$get('PlatFormAPI/ServicePackItem/GetServicePackItemBySPRId',this.$pick({
                ServicePackRecordId: this.$route.query.id
            })).then(o => {
                if(!o.length){
                    this.$toast('暂无需要人工登记的服务项')
                }
                else{
                    this.$router.push(`/record-checkIn?id=${this.$route.query.id}`)
                }
            })
        }
    }
}



